import * as React from 'react';
import { Collapse, Box, Button } from "@chakra-ui/core";

const Collapsable = (props) => {

    const [show, setShow] = React.useState(false);
    const handleToggle = () => setShow(!show);

    return (
        <Box mb="1rem">
            <Collapse {...props} isOpen={show}>
                {props.children}
            </Collapse>
            <Button size="sm" onClick={handleToggle} mt="1rem" variantColor="pink" variant="solid">
                Show {show ? "Less" : "More"}
            </Button>
        </Box>
    );
};

export default Collapsable;